const CustomLoader = styled.div`
	border-radius: 60px;
	border: 3px solid #fff;
	height: 80px;
	width: 80px;
	position: relative;

	top: 28%;
	top: -webkit-calc(50% - 43px);
	top: calc(50% - 43px);
	left: 35%;
	left: -webkit-calc(50% - 43px);
	left: calc(50% - 43px);

:after{
	content: "";
	position: absolute;
	background-color: #fff;
	top:2px;
	left: 48%;
	height: 38px;
	width: 4px;
	border-radius: 5px;
	-webkit-transform-origin: 50% 97%;
			transform-origin: 50% 97%;
	-webkit-animation: grdAiguille 2s linear infinite;
			animation: grdAiguille 2s linear infinite;
}

@-webkit-keyframes grdAiguille{
    0%{-webkit-transform:rotate(0deg);}
    100%{-webkit-transform:rotate(360deg);}
}

@keyframes grdAiguille{
    0%{transform:rotate(0deg);}
    100%{transform:rotate(360deg);}
}

:before{
	content: "";
	position: absolute;
	background-color: #fff;
	top:6px;
	left: 48%;
	height: 35px;
	width: 4px;
	border-radius: 5px;
	-webkit-transform-origin: 50% 94%;
			transform-origin: 50% 94%;
	-webkit-animation: ptAiguille 12s linear infinite;
			animation: ptAiguille 12s linear infinite;
}

@-webkit-keyframes ptAiguille{
    0%{-webkit-transform:rotate(0deg);}
    100%{-webkit-transform:rotate(360deg);}
}

@keyframes ptAiguille{
    0%{transform:rotate(0deg);}
    100%{transform:rotate(360deg);}
}
`
const ButtonInput = forwardRef(({ value, onClick }, ref) => (
    <button
        onClick={onClick}
        ref={ref}
        type="button"
        className='inline-flex justify-start py-2 px-3 w-full text-sm font-medium text-gray-700 bg-white rounded border border-gray-300 shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-0 focus:ring-blue-500'
    >
        {format(new Date(value), 'dd MMMM yyyy')}
    </button>
))

const Home = () =>{
      const [data, setData] = useState([])
      const [startDate, setStartDate] = useState(new Date().setDate(new Date().getDate() - 2));
      const [endDate, setEndDate] = useState(new Date().setDate(new Date().getDate() - 0))
      const [loading, setLoading] = useState(true)
      const [openDropdown, setOpenDropdown] = useState(false)
      const [after, setAfter] = useState(0)
      const [afterNext, setAfterNext] = useState(0)
      const [total, setTotal] = useState(0)
      
      const [selectedProperties, setSelectedProperties] = useState(properties)
      const [selectedStage, setSelectedStage] = useState('')

      const signOutUser = async() =>{
        try{
          await signOut(auth).then(()=>{
            console.error("Se cerro session");
          })
        }catch(error){
          console.log(error)
        }
      }
      
      useEffect(() => {
        setLoading(true)
      }, [])
      
      useEffect(() => {
        if (startDate > endDate) {
          setStartDate(endDate)
        }
      }, [endDate, startDate])

      useEffect(() => {
        if (startDate > endDate) {
          setEndDate(startDate)
        }
      }, [endDate, startDate])
      
      const getDataDeals = async () => {
        setLoading(true)

        let start = startDate;
        let end = endDate;

        if (typeof start !== 'number') {
          start = new Date(startDate).getTime()
        }

        if (typeof end !== 'number') {
          end = new Date(endDate).getTime()
        }

        let url = `api/deals?start=${start}&end=${end}&after=${after}`
        
        if (selectedStage) {
          url += `&stage=${selectedStage}`
        }

        const response = await axios.get(process.env.REACT_APP_MOCK_THIS_IS_NOT_A_KEY + url);

        if (after === 0) {
          setData(response.data.results);
        } else {
          setData([...data, ...response.data.results]);
        }
        
        setAfterNext(response.data?.paging?.next?.after)
        setLoading(false)
        setTotal(response.data?.total)
      };

      useEffect(() => {
        getDataDeals();
      // eslint-disable-next-line react-hooks/exhaustive-deps
      }, [after]);

      const handlePropertyChange = (item, checked) => {
        if (checked) {
          setSelectedProperties([...selectedProperties, item].sort((a, b) => a.order - b.order))
        } else {
          setSelectedProperties(selectedProperties.filter(i => i !== item).sort((a, b) => a.order - b.order))
        }
      }

      const handleSearch = () => {
        if (after === 0) {
          setData([])
          setTimeout(() => {
            getDataDeals();
          }, 200);
        } else {
          setAfter(0); 
        }
      }

      const handleShowMore = () => {
        setAfter(afterNext);
      }

      return (
      <>
        <div className="relative h-screen bg-gray-800">
          <nav className="bg-white">
            <div className="px-2 mx-auto max-w-7xl sm:px-6 lg:px-8">
              <div className="flex relative justify-between items-center h-16">
        
                <div className="flex flex-1 justify-center items-center sm:items-stretch sm:justify-start">
                  <div className="flex flex-shrink-0 items-center">
                    <img className="block w-auto h-8 lg:hidden" src="https://i.ibb.co/Zf0t30M/1.png" alt="Workflow"/>
                    <img className="hidden w-auto h-8 lg:block" src="https://i.imgur.com/9LPmT4m.png" alt="Workflow"/>
                  </div>
                  <div className="hidden sm:block sm:ml-6">
                    <div className="flex space-x-4">
                      <span
                        onClick={()=>{
                          signOutUser()
                        }}
                      className="py-2 px-3 text-sm font-medium text-white bg-gray-900 rounded-md cursor-pointer hover:bg-gray-800" aria-current="page">
                        Sign Out
                      </span>
                    </div>
                  </div>
                </div>
                
              </div>
            </div>
        
            <div className="sm:hidden" id="mobile-menu">
              <div className="px-2 pt-2 pb-3 space-y-1">
                <span className="block py-2 px-3 text-base font-medium text-white bg-gray-900 rounded-md" aria-current="page">
                  Cerrar Sesion
                </span>
              </div>
            </div>
          </nav>     
          
          <div className="container flex items-end px-4 pt-10 mx-auto">
            <div>
              <div className="mr-4">
                <p className="mb-1 font-medium text-white">Date Range</p>
                <div className="flex space-x-2">
                  <div className="relative w-40">
                    <DatePicker
                      selected={startDate}
                      onChange={(date) => setStartDate(date)}
                      selectsStart
                      startDate={startDate}
                      endDate={endDate}
                      nextMonthButtonLabel=">"
                      previousMonthButtonLabel="<"
                      popperClassName="react-datepicker-left"
                      customInput={<ButtonInput />}
                      renderCustomHeader={({
                        date,
                        decreaseMonth,
                        increaseMonth,
                        prevMonthButtonDisabled,
                        nextMonthButtonDisabled,
                      }) => (
                        <div className="flex justify-between items-center py-2 px-2">
                          <span className="text-lg text-gray-700">
                            {format(date, 'MMMM yyyy')}
                          </span>
                          <div className="space-x-2">
                            <button
                              onClick={decreaseMonth}
                              disabled={prevMonthButtonDisabled}
                              type="button"
                              className={`
                                ${prevMonthButtonDisabled && 'cursor-not-allowed opacity-50'}
                                inline-flex p-1 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-0 focus:ring-blue-500
                              `}
                            >
                              <ChevronLeftIcon className="w-5 h-5 text-gray-600" />
                            </button>
                            <button
                              onClick={increaseMonth}
                              disabled={nextMonthButtonDisabled}
                              type="button"
                              className={`
                                ${nextMonthButtonDisabled && 'cursor-not-allowed opacity-50'}
                                inline-flex p-1 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-0 focus:ring-blue-500
                              `}
                            >
                              <ChevronRightIcon className="w-5 h-5 text-gray-600" />
                            </button>
                          </div>
                        </div>
                      )}
                    />
                  </div>
                  <div className="relative w-40">
                    <DatePicker
                      selected={endDate}
                      onChange={(date) => setEndDate(date)}
                      selectsEnd
                      startDate={startDate}
                      endDate={endDate}
                      nextMonthButtonLabel=">"
                      previousMonthButtonLabel="<"
                      popperClassName="react-datepicker-right"
                      customInput={<ButtonInput />}
                      renderCustomHeader={({
                        date,
                        decreaseMonth,
                        increaseMonth,
                        prevMonthButtonDisabled,
                        nextMonthButtonDisabled,
                      }) => (
                        <div className="flex justify-between items-center py-2 px-2">
                          <span className="text-lg text-gray-700">
                            {format(date, 'MMMM yyyy')}
                          </span>

                          <div className="space-x-2">
                            <button
                              onClick={decreaseMonth}
                              disabled={prevMonthButtonDisabled}
                              type="button"
                              className={`
                                ${prevMonthButtonDisabled && 'cursor-not-allowed opacity-50'}
                                inline-flex p-1 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-0 focus:ring-blue-500
                              `}
                            >
                              <ChevronLeftIcon className="w-5 h-5 text-gray-600" />
                            </button>
                            <button
                              onClick={increaseMonth}
                              disabled={nextMonthButtonDisabled}
                              type="button"
                              className={`
                                ${nextMonthButtonDisabled && 'cursor-not-allowed opacity-50'}
                                inline-flex p-1 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-0 focus:ring-blue-500
                              `}
                            >
                              <ChevronRightIcon className="w-5 h-5 text-gray-600" />
                            </button>
                          </div>
                        </div>
                      )}
                    />
                  </div>
                </div>
              </div>
            </div>
            <div>
              <div className="mx-4">
                <p className="mb-1 font-medium text-white">Stage</p>
                <label className="block text-left">
                  <select 
                    className="inline-flex items-center py-2 px-2 w-60 font-semibold text-gray-500 bg-white rounded-lg hover:text-blue-500 focus:outline-none focus:shadow-outline md:px-4"                  
                    onChange={(e) => setSelectedStage(e.target.value)}
                  >
                    <option value=""> All Stages </option>
                      {stages.map((stage, index) =>
                        <option key={index} value={stage.value}> {stage.name} </option>
                      )}
                  </select>
                </label>
              </div>
            </div>
            <div>
              <div className="mx-4">
                <p className="mb-1 font-medium text-white">Properties to show</p>
                <div className="flex rounded-lg shadow">
                  <div className="relative">
                    <button 
                      onClick={() => setOpenDropdown(!openDropdown)}
                      className="inline-flex items-center py-2 px-2 w-60 font-semibold text-gray-500 bg-white rounded-lg hover:text-blue-500 focus:outline-none focus:shadow-outline md:px-4">
                      <svg xmlns="http://www.w3.org/2000/svg" className="w-6 h-6 md:hidden" viewBox="0 0 24 24"
                        strokeWidth="2" stroke="currentColor" fill="none" strokeLinecap="round"
                        strokeLinejoin="round">
                        <rect x="0" y="0" width="24" height="24" stroke="none"></rect>
                        <path
                          d="M5.5 5h13a1 1 0 0 1 0.5 1.5L14 12L14 19L10 16L10 12L5 6.5a1 1 0 0 1 0.5 -1.5" />
                      </svg>
                      <span className="hidden md:block">Display</span>
                      <svg xmlns="http://www.w3.org/2000/svg" className="ml-auto w-5 h-5" width="24" height="24"
                        viewBox="0 0 24 24" strokeWidth="2" stroke="currentColor" fill="none"
                        strokeLinecap="round" strokeLinejoin="round">
                        <rect x="0" y="0" width="24" height="24" stroke="none"></rect>
                        <polyline points="6 9 12 15 18 9" />
                      </svg>
                    </button>

                    {openDropdown && 
                      <div 
                        onClick={() => setOpenDropdown(false)}
                        className="block overflow-hidden absolute top-0 right-0 z-40 py-1 mt-12 -mr-1 w-60 bg-white rounded-lg shadow-lg"
                      >
                        {properties.map((property, index) => 
                          <div key={index}>
                            <label
                              className="flex justify-start items-center py-2 px-4 text-truncate hover:bg-gray-100">
                              <div className="mr-3 text-teal-600">
                                <input 
                                  type="checkbox" 
                                  className="form-checkbox focus:outline-none focus:shadow-outline" 
                                  checked={selectedProperties.includes(property)} 
                                  onChange={(e) => handlePropertyChange(property, e.target.checked)}
                                />
                              </div>
                              <div className="text-gray-700 select-none">
                                { property.name }
                              </div>
                            </label>
                          </div>
                        )}
                      </div>
                    }
                  </div>
                </div>
              </div>
            </div>
            <div>
              <button 
                onClick={handleSearch}
                className="p-2 pr-5 pl-5 mx-4 text-lg text-green-300 rounded-lg border-2 border-green-300 transition-colors duration-700 transform hover:bg-green-400 hover:text-gray-100 focus:border-4 focus:border-indigo-300">
                Search
              </button>
            </div>
            { after != null && 
              <div>
                <button 
                  onClick={handleShowMore}
                  className="p-2 pr-5 pl-5 mx-4 text-lg text-green-300 rounded-lg border-2 border-green-300 transition-colors duration-700 transform hover:bg-green-400 hover:text-gray-100 focus:border-4 focus:border-indigo-300">
                  Show 100 entries more
                </button>
              </div>
            }
            <div className="ml-auto">
              {data.length > 0 && 
                <div>
                  <ExcelFile element={
                    <button 
                      className="p-2 pr-5 pl-5 text-lg text-green-300 bg-transparent rounded-lg border-2 border-green-300 transition-colors duration-700 transform hover:bg-green-400 hover:text-gray-100 focus:border-4 focus:border-indigo-300">
                      Download Excel
                    </button>
                  }>
                    <ExcelSheet data={data} name={`Contacts`} >
                      {selectedProperties.map((property, index) => 
                        <ExcelColumn key={index} label={property.name} value={getDealStage(property.value)}/>
                      )}
                    </ExcelSheet>           
                  </ExcelFile>
                </div>
              }
            </div>
          </div>
          
          <div className="container py-6 px-4 mx-auto">
            <div className="flex justify-between items-center">
              <h1 className="py-4 text-3xl text-white">
                Entries  
              </h1>
              <p className="text-white">
                Showing {data.length} entries of {total}
              </p>
            </div>

            
            { loading &&  <CustomLoader className="my-4" /> }
            
            <div className="overflow-x-auto overflow-y-auto relative h-full bg-white rounded-lg shadow">
              <table className="relative w-full h-14 bg-white border-collapse table-auto whitespace-no-wrap table-striped">
                <thead>
                  <tr className="text-left">
                    {selectedProperties.map((property, index) => 
                      <th 
                        className="sticky top-0 py-2 px-6 text-xs font-bold tracking-wider text-gray-600 uppercase whitespace-nowrap bg-gray-100 border-b border-gray-200"
                        key={index}
                      >
                          { property.name }
                      </th>
                    )}
                  </tr>
                </thead>
                {data.length > 0 && 
                  <tbody>
                    {data.map((item, index) => 
                      <tr key={index}>
                        {selectedProperties.map((property, index) => 
                          <td key={index} className="border-t border-gray-200 border-dashed">
                            <span className="flex items-center py-3 px-6 text-gray-700">
                              { getDealStage(item[property.value]) }
                            </span>
                          </td>
                        )}
                      </tr>
                    )}
                  </tbody>
                }
              </table>
            </div>
            
            { after != null && 
              <div className='flex mx-auto mt-10 w-full'>
                <button 
                  onClick={handleShowMore}
                  className="p-2 pr-5 pl-5 mx-auto text-lg text-green-300 rounded-lg border-2 border-green-300 transition-colors duration-700 transform hover:bg-green-400 hover:text-gray-100 focus:border-4 focus:border-indigo-300">
                  Show 100 entries more
                </button>
              </div>
            }
            
          </div>

        </div>
      </>)
}

export default Home 